html {
  box-sizing: border-box;
  font-size: 16px; }

* {
  box-sizing: inherit; }

*:before {
  box-sizing: inherit; }

*:after {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-weight: normal;
  display: block;
  box-sizing: border-box;
  background-color: #f8f8f8; }

